@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500&display=swap');


.home-main {
  display: grid;
  grid-template-columns: 3fr 2fr;
  /* max-height: 50rem; */
  padding-top: 5rem;
  margin-right: 6px;
}

.home-main::-webkit-scrollbar {
  display: none;
}


.main-right {
  max-height: 50rem;
  margin-top: -4.5rem;
  background-color: #fdb74f;
}

.main-img {
  width: 23rem;
  padding-top: 4rem;
}

.main {
  padding-top: 5rem;
}

.main-heading {
  font-size: 5rem;
  font-family: "Oswald";
}

.main-heading-india {
  font-size: 12rem;
  color: #f05a27;
  font-family: "Oswald";
  margin-top: -2rem;
  margin-bottom: 2rem;
}

.main-attributes {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 12rem;
  align-items: center;
  justify-content: center;
}

.attribute {
  padding-bottom: 2rem;
}

.attribute-text {
  color: #f05a27;
  font-weight: 600;
}

.attribute-number {
  font-weight: 500;
  border: 2px solid #fdb74f;
  border-radius: 2rem;
  width: fit-content;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0.4rem 0.8rem;
}

.attribute-number:hover {
  background-color: #fdb74f;
  color: white;
  cursor: pointer;
}

.home-show {
  margin: 5rem;
  margin-top: 12rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.show {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 84rem;
  border-radius: 2rem;
  padding: 2rem 4rem;
  margin-bottom: 4rem;
}

.show-language {
  background-color: rgba(252, 196, 113, 0.882)
}

.show-box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.show-icon {
  width: 2rem;
}

.icon2:hover {
  fill: gold
}


.icon4:hover {
  fill: red
}

.show-heading {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  align-self: start;
}

.show-btn {
  font-weight: 500;
  text-decoration: none;
  color: black;
  align-self: flex-end;
}

.show-btn:hover {
  color: #f05a27;
}

.show-details {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.last-box:hover {
  background-color: #fed495;
  color: white;
}

.detail-box {
  margin: 0.6rem;
  background-color: white;
  font-size: 1.8rem;
  padding: 1rem;
  border-radius: 0.8rem;
}

.show-details-art {
  display: flex;
  justify-content: space-between;
}

.card {
  margin: 1rem;
}

.card-img {
  border-radius: 0.6rem;
  margin: 0.4rem;
}

.card-label {
  margin: auto;
  margin-bottom: 0.6rem;
  background-color: #d3d3d3;
  padding: 0.1rem 0.8rem;
  border-radius: 1rem;
}