@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap");

*,
*::before,
*::after {
  padding: 0;
  margin: 4px 3px;
  box-sizing: border-box;
}

.App {
  font-family: "Poppins", sans-serif;
  text-align: center;
  /* position: relative; */
}


.header {
  /* position: absolute; */
  position: fixed;
  width: 100%;
  padding: 0px 10px;
  padding-top: 0.8rem;
  height: 5rem;
  display: grid;
  grid-template-columns: 3fr 2fr;
  justify-content: center;
  align-items: center;
  /* background-color: #fdb74f; */
  /* background: linear-gradient(-90deg, #fdb74f 39.8%, WHITE 0%); */
  z-index: 999;
}

.header-right {
  /* background-color: #fdb74f; */
  /* width: 39.5%; */
  display: flex;
  justify-content: flex-end;
  margin-left: -0.07rem;
  height: 100%;
  width: 99.4%;
}

.btn-right {
  color: black;
  font-size: large;
  font-weight: 500;
  text-decoration: none;
  text-transform: capitalize;
  background-color: #f05a27;
  justify-content: center;
  align-items: center;
  margin: 1.5rem;
  /* margin-right: 4rem; */
  padding: 0rem 0.8rem;
  /* border-radius: 0.5rem; */
  box-shadow: 8px 8px 0px 0px #41220d;

}

.btn-right:last-child {
  margin-right: 5rem;
}

.header-left {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  justify-content: center;
}

.btn-left {
  color: black;
  font-weight: 500;
  font-size: 1.3rem;
  text-decoration: none;
  text-transform: capitalize;
}